	padding-left: 6px;
	list-style: none;
	counter-reset: item;
	margin: 0px;
}

.payment-guide .card .card-body ol li {
	counter-increment: item;
	margin-bottom: 6px;
}

.payment-guide .card .card-body ol li::before {
	margin-right: 10px;
	content: counter(item);
	background: #b8b8b8;
	border-radius: 100%;
	color: white;
	width: 1.5em;
	text-align: center;
	display: inline-block;
}

.payment-guide .card .card-header button .up,
.payment-guide .card .card-header button.collapsed .down {
	display: block;
}

.payment-guide .card .card-header button .down,
.payment-guide .card .card-header button.collapsed .up {
