/* The actual timeline (the vertical ruler) */
.timeline {
	position: relative;
}

/* The actual timeline (the vertical ruler) */
.timeline::after {
	/* content: "";
	position: absolute;
	width: 3px;
	background-color: white;
	top: 0;
	bottom: 0; */
}

/* Container around content */
.container-track {
	padding: 0px 0px 0px 30px;
	position: relative;
	background-color: inherit;
}

/* The line vertical timeline */
.container-track::before {
	content: "";
	position: absolute;
	width: 3px;
	background-color: #c3c3c3;
	top: 0;
	bottom: 0;
	left: 0;
	margin-left: -5px;
	margin-top: 5px;
}

/* The circles on the timeline */
.container-track::after {
	content: "";
	position: absolute;
	width: 24px;
	height: 24px;
	right: -17px;
	background-color: #c3c3c3;
	top: 1px;
	border-radius: 50%;
	z-index: 1;
}

.container-track.end::after {
	content: "";
	position: absolute;
	width: 24px;
	height: 24px;
	right: -17px;
	background-color: #32a02d;
	top: 1px;
	border-radius: 50%;
	z-index: 1;
}

.container-track.end::before {
	content: "";
	position: absolute;
	width: 3px;
	background-color: #32a02d;
	top: 0;
	bottom: 0;
	left: 0;
	margin-left: -5px;
	margin-top: 5px;
}

/* The line vertical timeline */
.container-track.start::before {
	content: "";
	position: absolute;
	width: 3px;
	background-color: transparent;
	top: 0;
	bottom: 0;
	left: 0;
	margin-left: -5px;
	margin-top: 5px;
}

/* The circles on the timeline */
.container-track.start::after {
	content: "";
	position: absolute;
	width: 24px;
	height: 24px;
	right: -17px;
	background-color: #c3c3c3;
	top: 1px;
	border-radius: 50%;
	z-index: 1;
}

.right {
	left: 1.4%;
}

/* Fix the circle for containers on the right side */
.right::after {
	left: -16px;
}

